<template>
  <div>
    <backHomeBtn>
      <template>
        <div id="qrCodeDiv"></div>
      </template>
    </backHomeBtn>
  </div>
</template>

<script>
import QRCode from "qrcodejs2";

export default {
  name: "qrCode",
  components: {},
  props: {},
  data() {
    return {};
  },
  methods: {
    /**
     * @Event 方法
     * @description: 生成二维码
     * @info: 必須放在mounted中
     * */
    creatQrCode() {
      let qrCodeDiv = document.getElementById("qrCodeDiv");
      const qrcode = new QRCode(qrCodeDiv, {
        width: 200, // 高度
        height: 200, // 宽度
        colorDark: "#000000", //前景色
        colorLight: "#ffffff", //后景色
        correctLevel: QRCode.CorrectLevel.H, //容错级别 QRCode.CorrectLevel.L QRCode.CorrectLevel.M QRCode.CorrectLevel.Q QRCode.CorrectLevel.H
      });
      const text = "https://chat.wuguokai.cn/#/chat/1684339872634";
      // qrCode.clear() //清除二维码，仅仅在不支持canvas的浏览器下有用
      qrcode.makeCode(text); //手动设置 用于生成二维码的文本
    },
  },
  created() {},
  mounted() {
    this.creatQrCode();
  },
};
</script>

<style lang="scss" scoped>
#qrCodeDiv {
  margin: 20px 0 0 0;
}
</style>
